.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  .content {
    background-color: white;
    padding: 2rem;
    box-shadow: 0 10px 20px -15px rgb(0 0 0 / 50%);
    background: rgba($color: #ffffff, $alpha: 1);
    border: 1px solid #ccc;
    border-radius: 0.25rem;
    width: 75%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    .actions {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      gap: 1rem;
      border-collapse: collapse;

      padding: 0.25rem;
      &__search {
        min-width: 35%;
        display: flex;
        gap: 1rem;
        align-items: center;
        justify-content: space-between;
        position: relative;
        label {
          font-size: 1rem;
          font-weight: 600;
        }
        &__input {
          width: 100%;
          display: flex;
          position: relative;
          .form-control {
            width: 100%;
            padding: 0.5rem;
            border: 1px solid #ccc;
            border-radius: 0.25rem;
            border-top-right-radius: 0px;
            border-bottom-right-radius: 0px;
            border-right: none;

            &:focus {
              outline: none;
              border: 1px solid #007bff;
            }
          }
          button {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            border-left: none;
          }
        }
      }
      .col {
        display: flex;
        flex-direction: column;
        gap: 1rem;
      }
      &__expire {
        display: flex;
        gap: 1rem;
        align-items: baseline;
        .form-control {
          width: 100%;
          padding: 0.5rem;
          border: none;
          border-bottom: 1px solid #aaa;
          padding-bottom: 0.5rem;
          border-radius: 0;
          background-color: transparent;

          &:focus {
            outline: none;
            border: 1px solid #007bff;
          }
        }
      }
    }
  }
}
