.horizantal-layout {
  display: grid;
  min-width: 100%;
  min-height: 100vh;
  grid-template-rows: auto 1fr;
  &__content {
    position: relative;
    .background {
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // apply background color animation
      background-color: #153851;
      animation: background-color 10s infinite;
      opacity: 0.85;
      // filter: blur(2px);
      // filter: grayscale(0.5);
      // filter: saturate(0.9);
      // filter: sepia(0.5);
      filter: contrast(0.75);
    }
    .content {
      z-index: 2;
      opacity: 1;
    }
  }
}

// create animation for background changing color
@keyframes background-color {
  0% {
    background-color: #f7e5e5;
  }
  25% {
    background-color: #f7e5f4;
  }
  50% {
    background-color: #e5eaf7;
  }
  75% {
    background-color: #f7e5f4;
  }
  100% {
    background-color: #f7e5e5;
  }
}
